@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cabin&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Antonio&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  height: 100%;
  background-color: #F4F6F8;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Search {
	background-color: white;

	margin-bottom: 2.5vh;

	border-style: solid;
	border-color: #6497bf;
	border-width: 1px;

	padding-left: min(2vw, 15px);
	padding-right: min(2vw, 15px);
	padding-top: min(3vh, 30px);
	padding-bottom: min(4vh, 30px);

}

.Search p {
	font-family: 'Cabin', sans-serif;
	font-weight: bold;
	font-size: 120%;

	margin-top: 1vh;
	margin-bottom: 1vh;
}

.Search input {
	width: calc(100% - 2vw);
}
.MovieList {
	font-family: 'Cabin', sans-serif;

	background-color: white;

	width: calc(50% - 5vw);

	margin-top: 3.5vh;
	margin-bottom: 3.5vh;
	margin-right: 1vw;

	border-style: solid;
	border-color: #6497bf;
	border-width: 1px;

	padding-left: 2vw;
	padding-right: 2vw;
	padding-top: 3vh;
	padding-bottom: 4.5vh;
}

.MovieList .entry {
	margin-bottom: 0.5vh;

	border-style: solid;
	border-color: #6497bf;
	border-width: 2px;
	border-radius: 3px;

	padding-left: 1vw;
	padding-right: 1vw;
	padding-top: 0.7vh;
	padding-bottom: 0.7vh;

}

.MovieList .KeyWord {
	font-weight: bold;
	font-size: 120%;

	margin-top: 1vh;
	margin-bottom: 1vh;
}

.MovieList .Nominate {
	background-color: #6497bf;
	color: white;

	margin-right: 1vw;

	border-style: solid;
	border-color: #6497bf;
	border-width: 2px;
	border-radius: 3px;

	padding-left: 0.6vw;
	padding-right: 0.6vw;
	padding-top: 0.6vh;
	padding-bottom: 0.6vh;

	transition-duration: 0.3s;
}

.MovieList .Nominate:hover {
	background-color: #325875;

	border-color: #325875;

	padding-left: 0.6vw;
	padding-right: 0.6vw;
	padding-top: 0.6vh;
	padding-bottom: 0.6vh;
}

.MovieList .Nominate:disabled {
	background-color: white;
	color: #6497bf;

	border-style: solid;
	border-color: #6497bf;
	border-width: 2px;
	border-radius: 3px;
}


.Nomination {
	font-family: 'Cabin', sans-serif;

	background-color: white;

	width: calc(50% - 5vw);

	margin-top: 3.5vh;
	margin-bottom: 3.5vh;
	margin-left: 1vw;

	border-style: solid;
	border-color: #6497bf;
	border-width: 1px;

	padding-left: 2vw;
	padding-right: 2vw;
	padding-top: 3vh;
	padding-bottom: 4.5vh;

}

.Nomination .entry {
	margin-bottom: 0.5vh;

	border-style: solid;
	border-color: #f44336;
	border-width: 2px;
	border-radius: 3px;

	padding-left: 1vw;
	padding-right: 1vw;
	padding-top: 0.7vh;
	padding-bottom: 0.7vh;

}

.Nomination .KeyWord {
	font-weight: bold;
	font-size: 120%;

	margin-top: 1vh;
	margin-bottom: 1vh;
}


.Nomination .Remove {
	background-color: #f44336;
	color: white;

	margin-right: 1vw;

	border-style: solid;
	border-color: #f44336;
	border-width: 2px;
	border-radius: 3px;

	padding-left: 0.6vw;
	padding-right: 0.6vw;
	padding-top: 0.6vh;
	padding-bottom: 0.6vh;

	transition-duration: 0.3s;
}

.Nomination .Remove:hover {
	background-color: #b01b10;

	border-color: #b01b10;

	padding-left: 0.6vw;
	padding-right: 0.6vw;
	padding-top: 0.6vh;
	padding-bottom: 0.6vh;
}

.Nomination .Remove:disabled {
	background-color: white;
	color: #f44336;

	border-style: solid;
	border-color: #f44336;
	border-width: 2px;
	border-radius: 3px;
}

.App {
  /*margin-top: 10vh;*/
  margin-left: 10vw;
  margin-right: 10vw;
}

.App-header {
  font-family: 'Antonio', sans-serif;
  font-size: 150%;
  text-align: center;
}

.App .Banner {
  padding-top: 1%;
  padding-bottom: 1%;

  border-style: solid;
  border-color: darkred;
  border-width: 3px;

  background-color: red;
  color: white;

  font-size: 120%;
  font-weight: bold;
  text-align: center;

}

.App .Container {
  display: -webkit-flex;
  display: flex;
}


